<template lang="html">
    <li class="sf-menuv__item" :class="{'sf-menuv__item--open': (hasSub&&showSub)||actived, 'sf-menuv__item--actived': actived, 'sf-menuv__item--collapse': isCollapse}">
        <div
            class="sf-menuv__title"
            :style="{'padding-left': indent}"
            @click="$emit('click');toggle();"
            @mouseenter="displaySub"
            @mouseleave="hideSub"
        >
            <span class="sf-menuh__icon" v-show="hasIcon">
                <slot name="icon"></slot>
            </span>
            <span
                class="sf-menuv__text"
                v-show="isNormal||showSub"
            >
                <slot></slot>
            </span>
            <template v-if="hasSub&&isNormal">
                <i class="fa fa-angle-up sf-menuv__arrow" v-show="showSub"></i>
                <i class="fa fa-angle-down sf-menuv__arrow" v-show="!showSub"></i>
            </template>
        </div>
        <transition :name="isCollapse?'none':'slide'">
            <div
                class="sf-menuv__submenu"
                v-if="hasSub"
                v-show="showSub"
                @mouseenter="displaySub"
                @mouseleave="hideSub"
            >
                <slot name="submenu"></slot>
            </div>
        </transition>
    </li>
</template>

<script>

import isArray from 'lodash/isArray'

export default {
    name: 'menuv-item',
    props: {
        actived: false
    },
    data() {
        return {
            level: 0,
            showSub: false,
            mode: 'normal'
        }
    },
    computed: {
        indent() {
            if(this.mode==="normal"){
                return this.level*1+'rem';
            }else {
                return '1rem';
            }
        },
        hasSub() {
            return !!this.$slots.submenu;
        },
        hasIcon() {
            return !!this.$slots.icon;
        },
        isNormal() {
            return this.mode === 'normal';
        },
        isCollapse() {
            return this.mode === 'collapse';
        }
    },
    watch: {
        isCollapse(val) {
            if(val) {
                this.showSub = false;
            }
        },
        actived(val) {
            if(val) {
                this.hideSiblings();
                this.showSub = true;
                var parent = this.$parent.$parent;
                if(parent && parent.$options.name === this.$options.name && parent.mode === 'normal') {
                    parent.hideSiblings();
                    parent.showSub = true;
                }
            }
        }
    },
    methods: {
        addLevel() {
            this.level++;
            if(!isArray(this.$slots.submenu)) return;
            this.$slots.submenu.forEach(submenu=>{
                submenu.child.addLevel();
            })
        },
        // 切换是否显示子菜单
        toggle() {
            if(!this.isNormal) return;
            if(!this.showSub) {
                this.hideSiblings();
            }
            this.showSub = !this.showSub;
        },
        hideSub() {
            if(!this.isCollapse) return;
            clearTimeout(this.timeoutFlat);
            this.timeoutFlat = setTimeout(()=>{
                this.showSub = false;
            },300);
        },
        // 隐藏同级子菜单
        hideSiblings() {
            this.$parent.$children.forEach(item=>{
                if(item.$options.name!=='menuv-item') return;
                item.showSub = false;
            })
        },
        displaySub() {
            if(!this.isCollapse) return;
            clearTimeout(this.timeoutFlat);
            this.hideSiblings();
            this.showSub = true;
        },
        // 关闭所有的expand
        closeAll() {
            this.showSub = false;
        }
    }
}
</script>

<style lang="css">
    .sf-menuv__item {
        margin: 0;
        padding: 0;
        cursor: pointer;
        position: relative;
    }

    .sf-menuv__title {
        padding: 1em 1.5rem;
        line-height: 1.25;
        white-space: nowrap;
    }
    .sf-menuv__item:hover > .sf-menuv__title {
        background-color: #999;
        position: relative;
    }

    .sf-menuh__icon {
        width: 20px;
        display: inline-block;
        text-align: center;
    }

    .sf-menuv__arrow {
        float: right;
        font-size: 18px;
    }



    .sf-menuv__item--actived .sf-menuv__title {
        box-shadow: 3px 0px 0 0 #333 inset;
    }

    .slide-enter-active {
        overflow: hidden;
        max-height: 1000px;
        transition-property: all;
        transition-duration: .2s;
        transition-timing-function: cubic-bezier(0, 0, 1, 0);
    }
    .slide-leave-active {
        overflow: hidden;
        max-height: 1000px;
        transition-property: all;
        transition-duration: .2s;
        transition-timing-function: cubic-bezier(0, 1, 1, 1);
    }
    .slide-leave-to, .slide-enter {
        max-height: 0;
    }

    .sf-menuv__item--collapse > .sf-menuv__title > .sf-menuv__text {
        display: block;
        position: absolute;
        width: 220px;
        right: -220px;
        box-sizing: border-box;
        top: 0;
        padding: 1em 1.5rem;
        color: #666;
        border: 1px solid #ddd;
        z-index: 2;
        background-color: white;
    }
    .sf-menuv__item--collapse > .sf-menuv__title > .sf-menuv__text::before {
        content: "";
        display: block;
        width: 10px;
        height: 10px;
        background-color: rgb(250, 250, 250);
        left: -5px;
        top: 1.25em;
        position: absolute;
        transform: rotate(-45deg);
        border-top: 1px solid rgb(221, 221, 221);
        border-left: 1px solid rgb(221, 221, 221);
    }

    .sf-menuv__item--collapse > .sf-menuv__submenu {
        position: absolute;
        right: -220px;
        width: 220px;
        top: 100%;
        background-color: white;
        color: #666;
        box-sizing: border-box;
        border: 1px solid #ddd;
        z-index: 2;
        background-color: white;
    }

</style>



