<template lang="html">
    <li class="sf-node">
        <i
            class="sf-node__toggle" :class="{
                'fa': hasSub,
                'fa-caret-down': hasSub&&showSub,
                'fa-caret-right': hasSub&&!showSub
            }"
            @click="toggle"
        ></i><span class="sf-node__content">
            <slot name="title">{{title}}</slot>
        </span>
        <ul class="sf-node__sub" v-if="showSub">
            <slot></slot>
        </ul>
    </li>
</template>

<script>

import isArray from 'lodash/isArray'

export default {
    name: 'node',
    props: {
        title: {
            default: ''
        }
    },
    data() {
        return {
            hasSub: false,
            showSub: false
        }
    },
    mounted() {
        if(this.$slots.default) this.hasSub=true;
    },
    methods: {
        toggle() {
            this.showSub = !this.showSub;
        },
        expand(all) {
            this.showSub = true;
            if(!all) return;
            this.$nextTick(()=>{
                this.$children.forEach(node=>{ node.expand(true) })
            })
        },
        shrink() {
            this.showSub = false;
        }
    }
}
</script>




